@import url(https://fonts.googleapis.com/css?family=Montserrat:900|Raleway:400,400i,700,700i);
/* General Styles */
.app {
    font-family: 'Roboto', sans-serif;
    display: flex;
    flex-direction: column; /* Column layout for mobile */
    height: 100vh;
    background-color: #f1f3f4;
}

/* Sidebar (Contacts List) */
.sidebar {
    background-color: #ececec;
    width: 100%; /* Full width on mobile */
    padding: 10px; /* Smaller padding for mobile */
    box-shadow: none; /* Remove shadow on mobile */
}

.sidebar h3 {
    margin-bottom: 10px; /* Reduce margin for mobile */
    font-size: 1.2em; /* Slightly smaller font for mobile */
    font-weight: 500;
    color: #333;
}

.fetch-button {
    padding: 10px 20px;
    border: none;
    border-radius: 25px;
    background-color: #090927d7;
    color: white;
    cursor: pointer;
    margin-bottom: 20px;
    width: 100%;
    transition: background-color 0.3s;
}

.fetch-button:hover {
    background-color: #101042d7;
}

.senders-list {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.senders-list li {
    display: flex;
    align-items: center;
    padding: 10px 15px;
    margin-bottom: 10px;
    border-radius: 10px;
    background-color: #fff;
    cursor: pointer;
    transition: background-color 0.3s, box-shadow 0.3s;
}

.senders-list li:hover {
    background-color: #f0f0f0;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.1);
}

/* Chat Window */
.chat-container {
    width: 100%; /* Full width on mobile */
    display: flex;
    flex-direction: column;
    height: 100%; /* Fill available height */
    background-color: #ffffff;
}

.chat-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    background-color: #f1f1f1;
}
.chat-header button {
    padding: 8px 15px; /* Smaller padding for compact buttons */
    background-color: #222261d7; /* Blue color */
    color: #fff;
    border: none;
    border-radius: 25px;
    font-size: 14px; /* Smaller font size for buttons */
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease; /* Smooth animations */
}

.chat-header button:hover {
    background-color: #d44179; /* Darker blue on hover */
    transform: scale(1.05); /* Slightly enlarge on hover */
}

.chat-header button:active {
    background-color: #15153c; /* Even darker blue on click */
    transform: scale(0.95); /* Slight shrinking on click */
}

/* Aligning the "Add New Contact" and "Logout" buttons to the right */
.chat-header button:last-child {
    margin-left: 10px; /* Adds space between the buttons */
}

.chat-window {
    flex: 1 1; /* Allow it to grow and fill space */
    padding: 20px;
    overflow-y: auto;
    background-color: #ffffff;
    border-left: 1px solid #e0e0e0; /* Keeps the border consistent */
}

.messages {
    list-style: none;
    padding: 0;
    margin: 0; /* Ensure no extra margin */
}

.message {
    padding: 8px 12px;
    margin: 10px 0;
    border-radius: 10px;
    max-width: 60%;
    position: relative;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2), /* Main shadow */
                0px 1px 10px rgba(0, 0, 0, 0.1); /* Lighter shadow for depth */
    transition: box-shadow 0.3s; /* Smooth transition for shadows */
}


.message.sent:hover {
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.3),
                0px 2px 20px rgba(0, 0, 0, 0.15);
}
.message.sent {
    background: #afaebbc4;
    text-align: right;
    float: right;
    clear: both;
    animation: slideInRight 0.4s ease-out;
    border-radius: 20px;
    margin-right: 10px; /* Add some space to the right */
}

.message.received {
    background: #222261d7;
    text-align: left;
    float: left;
    clear: both;
    color: white;
    animation: slideInLeft 0.4s ease-out;
    border-radius: 20px;
    margin-left: 10px; /* Add some space to the left */
}

.message.received:hover {
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.3),
                0px 2px 20px rgba(0, 0, 0, 0.15);
}

.timestamp {
    display: block;
    font-size: 0.75em;
    color: #000;
    position: absolute;
    margin-top: 15px;
    right: 0;
    opacity: 0.8;
    text-align: right;
}

/* Input Area */
.input-area {
    display: flex;
    flex-direction: column; /* Stack inputs vertically on mobile */
    padding: 15px;
    background-color: #f7f7f7;
    border-top: 1px solid #e0e0e0;
    border-radius: 0 0 5px 5px;
}

.message-form {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    background-color: #f8f9fa; /* Light background for the message input area */
    border-top: 1px solid #ccc; /* Subtle border on top */
}

.message-form input[type="text"] {
    flex: 1 1; /* Take up available space */
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 25px;
    font-size: 16px;
    outline: none; /* Remove default outline on focus */
    background-color: #fff;
    margin-right: 10px; /* Space between input and button */
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Add slight shadow */
    transition: border-color 0.3s ease; /* Smooth border transition on focus */
}

.message-form input[type="text"]:focus {
    border-color: #222261d7; /* Change border color on focus */
}

.message-form button {
    padding: 10px 20px;
    background-color: #222261d7; /* Blue button */
    color: #fff;
    border: none;
    border-radius: 25px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease; /* Smooth animations */
}

.message-form button:hover {
    background-color: #d44179; /* Darker blue on hover */
    transform: scale(1.05); /* Slightly enlarge on hover */
}

.message-form button:active {
    background-color: #222261d7; /* Even darker blue on click */
    transform: scale(0.95); /* Slightly shrink on click */
}

.send-button, .addcontact-button ,.cancel-button ,.save-button ,.handlelogin-button,.handlesignup-button{
    padding: 12px 25px;
    border: none;
    border-radius: 25px;
    background-color: #090927d7;
    color: white;
    cursor: pointer;
    transition: background-color 0.3s;
    width: 100%; /* Full width on mobile */
}

.send-button:hover, .addcontact-button:hover ,.cancel-button ,.save-button {
    background-color: #064e45;
}


.h2 {
    font-size: 1.2em;
    color: #ffffff;
    margin: 0;
    padding: 10px 0;
    text-align: left;
    font-weight: bold;
    letter-spacing: 0.5px;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
    font-family: 'Helvetica Neue', 'Arial', sans-serif;
}

/* Animations */
@keyframes slideInRight {
    0% {
        transform: translateX(100%);
        opacity: 0;
    }
    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes slideInLeft {
    0% {
        transform: translateX(-100%);
        opacity: 0;
    }
    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

/* Media Queries for Responsive Design */
@media (min-width: 768px) {
    .app {
        flex-direction: row; /* Row layout for larger screens */
    }

    .sidebar {
        width: 25%; /* Sidebar takes 1/4th of the screen on larger screens */
        box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.1);
    }

    .chat-container {
        width: 75%; /* Chat container takes 3/4th of the screen */
    }

    .input-area {
        flex-direction: row; /* Row layout for inputs on larger screens */
    }

    .recipient-input,
    .message-input {
        margin-right: 10px;
        margin-bottom: 0;
        border-radius: 25px; /* Larger border radius */
    }

    .send-button {
        width: auto;
    }

    .messages {
        display: flex;
        flex-direction: column;
    }

    .message {
        max-width: 75%; /* Increase the width for larger screens */
    }

    .message.sent {
        align-self: flex-end; /* Align sent messages to the right on desktop */
    }

    .message.received {
        align-self: flex-start; /* Align received messages to the left on desktop */
    }
}
/*** FONTS ***/

/*** STYLE ***/
*,
*:before,
*:after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  background-color: #fafafa; /* Replaced $white with actual color value */
  color: #1d1f20; /* Replaced $black with actual color value */
  font-family: 'Raleway', sans-serif;
}

main {
  display: block;
  margin: 0 auto;
  max-width: 40rem;
  padding: 1rem;
}

.user-list {
  list-style: none;
  margin: 1.75rem 0;
  padding-left: 0; /* Remove default list styling */
}

.user-item {
  background: white;
  border-radius: 0 0.5rem 0.5rem 0.5rem;
  box-shadow: 0.25rem 0.25rem 0.6rem rgba(0, 0, 0, 0.05), 0 0.5rem 1.125rem rgba(75, 0, 0, 0.05); /* Replaced %boxshadow */
 
  min-height: 3rem;
  padding: 1rem 1rem 1rem 3rem;
  position: relative;
  cursor: pointer; /* Make the contact clickable */
  transition: background-color 0.3s ease;
  border: 1px solid #ddd; 
}

.user-item:hover {
  background-color: #f0f0f0; /* Change background on hover */
}

/* Remove numbering box shape (remove ::before) */
.user-item::before,
.user-item::after {
  content: none; /* Remove the numbering pseudo-elements */
}

.user-item a {
  text-decoration: none;
  color: inherit; /* Use the current color for links */
}

.user-item a:hover {
  text-decoration: underline;
}

/* Home.css */
.align {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh; /* Ensure full viewport height to center the content */
}

.grid {
    margin-left: auto;
    margin-right: auto;
    max-width: 320px;
    width: 90%;
    display: flex;
    justify-content: center; /* Center the form horizontally */
}

.login-box {
    background-color: #afaebbc4;
    padding: 20px;
    border-radius: 8px;
    width: 100%; /* Make box take full width within the grid */
    max-width: 400px; /* Restrict maximum width to avoid it becoming too wide */
    text-align: center;
    box-sizing: border-box; /* Include padding in the width calculation */
}

.text--center {
    text-align: center;
}

.form {
    margin: 1rem 0;
}

.form__field {
    margin: 0.5rem 0;
}

.form__input {
    width: 100%; /* Ensure input takes up the full width of the box */
    padding: 0.75rem;
    border-radius: 5px;
    border: 1px solid #ccc;
    background-color: #ffffff;
    color: #000000;
    font-size: 1rem;
    margin: 0.5rem 0;
    box-sizing: border-box; /* Ensure padding doesn't cause the field to overflow */
}

.form__input:focus {
    background-color: #fcfeffe7;
    outline: none;
}

button {
    background-color: #222261d7;
    color: #fff;
    border: none;
    padding: 1rem;
    font-size: 1rem;
    font-weight: bold;
    border-radius: 5px;
    cursor: pointer;
    margin: 1rem 0;
    width: 100%; /* Make the button full width within the box */
}

button:hover {
    background-color: #d44179;
}

.error {
    color: #ff6b6b;
    font-size: 1rem;
    margin-top: 1rem;
}

.password-input {
    position: relative;
    display: flex;
    align-items: center;
}

.eye-icon {
    position: absolute;
    right: 50rem;
    top: 24rem;
    cursor: pointer;
    color: #888;
}
